import Icon from "../../../../assets/icon.png";
import friend_1 from "../../../../assets/friend_1.png";
import friend_2 from "../../../../assets/friend_2.png";
import friend_3 from "../../../../assets/friend_3.png";
import InviteTitle from "../../../../assets/friendIcon_1.png";
import fanYongTitle from "../../../../assets/friendIcon_2.png";
export default {
  data() {
    return {
      Icon: Icon,
      friend_1: friend_1,
      friend_2: friend_2,
      friend_3: friend_3,
      InviteTitle: InviteTitle,
      fanYongTitle: fanYongTitle,
      noDate: false, //这是一个判断是否加载的开关
      page: 1,
      pageSize: 10,
      detail: {
        rake_back_amount: "0",
        buy_total: "0",
        count: "0"
      },
      list: []
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getRules() {
      this.$router.push("/inviteFriendsRule");
    },
    getInvite() {
      // 立即邀请
       this.$router.push({
          name: "haiBao",
          params: {
            activity_id: this.$route.params.activity_id
          }
        });
    },
    getFYDet() {
      // 查看返佣明细
      this.$router.push("/fanYongDet");
    },
    getData() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          offset: that.page, // 页码
          limit: 10 // 每页长度
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/invite/ranking-list",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            if (response.data.detail.buy_total == null) {
              response.data.detail.buy_total = 0;
            }
            if (response.data.detail.count == null) {
              response.data.detail.count = 0;
            }
            that.detail = response.data.detail;

            response.data.list.forEach(element => {
              if (element.user_name == "") {
                element.user_name = element.phone.substring(0, 3) + "****";
              } else{
                element.user_name=element.user_name.substring(0, 1) + "**";;
              }
            });
            if (that.page == 1) {
              that.list = response.data.list;
            } else {
              that.list = that.list.concat(response.data.list);
            }
          } else {
            that.isShow = false;
            that.noDate = true;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 上拉加载
    infinite(done) {
      console.log("上拉加载")
      if (this.noDate) {
        this.$refs.myscroller.finishInfinite(true); //这个方法是不让它加载了，显示“没有更多数据”，要不然会一直转圈圈
      } else {
        setTimeout(() => {
          this.page++; //下拉一次页数+1
          this.getData();
          done(); //进行下一次加载操作
        }, 3000);
      }
    },
    // 下拉刷新
    refresh() {
      console.log("下拉刷新")
      this.page = 1; //重置页数刷新每次页数都是第一页
      this.noDate = false; //重置数据判断
      setTimeout(
        function() {
          this.getData();
          this.$refs.myscroller.finishPullToRefresh(); //刷新完毕关闭刷新的转圈圈
        }.bind(this),
        1500
      );
    }
  }
};



